import axios from 'axios';

export default {
    fetch(url, params = {}) {
        return new Promise((resolve, reject) => {
            axios.get(url, {
                params: params
            })
                .then(response => {
                    resolve(response.data);
                })
                .catch(err => {
                    reject(err);
                })
        })
    },
    post(url, data = {}) {
        return new Promise((resolve, reject) => {
            axios.post(url, data)
                .then(response => {
                    resolve(response.data);
                }, err => {
                    reject(err);
                })
        })
    },
    patch(url, data = {}) {
        return new Promise((resolve, reject) => {
            axios.patch(url, data)
                .then(response => {
                    resolve(response.data);
                }, err => {
                    reject(err);
                })
        })
    },
    put(url, data = {}) {
        return new Promise((resolve, reject) => {
            axios.put(url, data)
                .then(response => {
                    resolve(response.data);
                }, err => {
                    reject(err);
                })
        })
    },
    delete(url, data = {}) {
        return new Promise((resolve, reject) => {
            axios.delete(url, data)
                .then(response => {
                    resolve(response.data);
                }), err => {
                reject(err);
            }
        })
    }
}
axios.defaults.timeout = 5000;
// axios.defaults.baseURL = 'http://www.baidu.com'; //

//http request 
axios.interceptors.request.use(
    config => {
        // config.data = JSON.stringify(config.data);
        config.proxy = {
            host:"127.0.0.1",
            port:8888
        }
        config.headers = {
            'Content-Type': 'application/json;charset=UTF-8'
        };
        return config;
    },
    error => {
        return Promise.reject(error);
    }
);

//
axios.interceptors.response.use(response => {
    return response
}, err => {
    if (err && err.response) {
        switch (err.response.status) {
            case 400:
                console.log('');
                break;
            case 401:
                console.log('');
                break;
            case 403:
                console.log('');
                break;
            case 404:
                console.log(',');
                break;
            case 405:
                console.log('');
                break;
            case 408:
                console.log('');
                break;
            case 500:
                console.log('');
                break;
            case 501:
                console.log('');
                break;
            case 502:
                console.log('');
                break;
            case 503:
                console.log('');
                break;
            case 504:
                console.log('');
                break;
            case 505:
                console.log('http');
                break;
            default:
                console.log(`${err.response.status}`)
        }
    } else {
        console.log('')
    }
    return Promise.resolve(err.response)
});
